}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1330px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.mouse {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99999;
  border-radius: 100%;
  width: 45px;
  height: 45px;
  pointer-events: none;
}
.mouse__view {
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 80px;
  height: 80px;
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
  background-color: #080808;
  opacity: 0;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out;
}
.view-visible .mouse__view {
  opacity: 1;
  transition: opacity 0.3s ease-in-out;
}
.mouse::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 2;
  border-radius: 100%;
  width: 35px;
  height: 35px;
  box-shadow: 4px 4px 4px #e2b8aa;
  background: #2d9687;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out;
}
.mouse::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 100%;
  width: 55px;
  height: 55px;
  background: #e2b8aa;
  transform: translate(-50%, -50%);
  transition: opacity 0.3s ease-in-out;
}

.links-visible::after {
  border: 4px solid #e2b8aa;
  background-color: transparent;
  transform: translate(-50%, -50%) scale(0.5);
  transition: 0.3s;
}

.links-visible::before {
  opacity: 0;
}

.burger {
  position: relative;
  z-index: 100;
  width: 40px;
  height: 32px;
}
.burger span {
  display: block;
  width: 100%;
  height: 2px;
  background-color: #e2b8aa;
  transition: all 1s cubic-bezier(1, 0.02, 0, 0.96);
}
.burger .first,
.burger .second,
.burger .third {
  position: absolute;
  left: 0;
}
.burger .first {
  top: 0;
}
.burger .second {
  top: 50%;
  margin-top: -1px;
}
.burger .third {
  bottom: 0;
}
.burger--active {
  transform: rotate(-90deg);
}
.burger--active .first,
.burger--active .second,
.burger--active .third {
  left: 0;
  animation: eye 3s ease-in-out infinite;
}
.burger--active .first,
.burger--active .second {
  top: -3px;
}
.burger--active .first {
  transform: rotate(60deg) translateX(50%);
}
.burger--active .second {
  margin-top: -1px;
  transform: rotate(-60deg) translateX(-50%);
}
.burger--active .third {
  bottom: -1px;
}
@keyframes eye {
  0% {
    background-color: red;
  }
  50% {
    background-color: blue;
  }
  100% {
    background-color: gold;
  }
}
.burger:hover span {
  background-color: #2d9687;
}

